import { useEffect, useState } from "react";
import axios from "axios";

export const useApi = (url, prName) => {

  const [result, setResult] = useState([])
  const [loading, setLoading] = useState(true)
  const [error, setError] = useState(false)

  useEffect(() => {

      axios.get(url).then(
        res => {
          const prData = res.data.find(pr => pr.path.toLowerCase() === prName.toLowerCase())
          setResult(prData)

          setTimeout(() => {
            setError(false)
            setLoading(false)
          }, 500)
        }
      )
      .catch(
        err => {
          setError(true)
          setLoading(false)
        }
      )
      .then(
        setLoading(true),
      )
  }, [url])

  return [ result, loading, error ]
}